export default {
	data() {
		return {
			showPassword1:true,
			showPassword2:true,
			password1: '',
			password2: ''
		}
	},
	methods: {
		changePassword1() {
			let that = this;
			that.showPassword1 = !that.showPassword1;
		},
		changePassword2() {
			let that = this;
			that.showPassword2 = !that.showPassword2;
		},
		register(){
			let that = this;
			if(!that.$utils.common.passwordTest.test(that.password1)){
				uni.showToast({
					title:'密码需是8-14位数字或字母',
					icon:'none'
				})
			}
			else if(that.password1!=that.password2){
				uni.showToast({
					title:'两次密码不一致',
					icon:'none'
				})
			}
			else{
				uni.showToast({
					title:'注册成功',
					icon:'success'
				})
			}
		}
	}
}
